
HTML, BODY {
  width: 100%;
  max-width: 100%;
  height: 100%;
}
.math {
  -webkit-box-shadow: 2px 2px 5px #888888;
          box-shadow: 2px 2px 5px #888888;
  font-size: 3.5em;
  font-family: verdana;
  font-weight: bold;
  color: #00f;
  border: 2px solid blue;
  max-width: 300px;
  width: 3em;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  margin-bottom: 8px;
  padding: 0px;
  line-height: .95em;
  padding-right: 30px;
  height: 3.5em;
}
.math DIV {
    text-align: right;
    margin-left: 30px;
}
.total {
  border-top: 10px solid blue;
  padding-top: 5px;
  margin-top: 12px;
}
.dice {
  width: 220px;
  height: 220px;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
  margin-bottom: 8px;
}
.dice TD {
    padding: 4px;
    background: #fff;
    border: 8px solid #fff;
}
.dice .d {
    background: blue;
}



